.top-block {
    width: 100vw;
    height: 470px;
    background: linear-gradient(225deg, #894AC9 0%, #4D46B1 100%);
    position: relative;
}

@media all and (max-width:380px) {
    .top-block {
        height: 419px;
    }
}

@media all and (max-width:320px) {
    .top-block {
        height: 262px;
    }
}

.top-block__header {
    position: relative;
    font-size: 34px;
    line-height: 41px;
    text-align: center;
    letter-spacing: 0.37px;
    padding-top: 80px;
    color: #FFFFFF;
}

@media all and (max-width:320px) {
    .top-block__header {
        font-size: 24px;
        letter-spacing: 0.261176px;
        padding-top: 50px;
    }
}

.top-block__close-bg {
    width: 32px;
    height: 32px;
    position: absolute;
    top: 50%;
    right: 3%;
    transform: translateY(-50%);
    border-radius: 50%;
    background: rgba(255, 255, 255, 0.527344);
}

.top-block__close-cross {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.top-block__pic {
    width: 112px;
    height: 250px;
    position: absolute;
    top: 28%;
    left: 45%;
    z-index: 3;
    filter: drop-shadow(10px 0px 20px rgba(0, 0, 0, 0.2));
}

@media all and (max-width:380px) {
    .top-block__pic {
        width: 95px;
        height: 213px;
        top: 30%;
    }
}

@media all and (max-width:320px) {
    .top-block__pic {
        width: 53px;
        height: 117px;
        top: 31%;
        left: 50%;
    }
}

.top-block__main {
    position: relative;
    width: 100%;
    height: 199px;
    margin-top: 33px;
}

@media all and (max-width:380px) {
    .top-block__main {
        height: 181px;
        margin-top: 20px;
    }
}

@media all and (max-width:320px) {
    .top-block__main {
        height: 102px;
        margin-top: 0px;
    }
}

.top-block__grid {
    width: 100%;
    height: 100%;
}

.top-block__grid_mini {
    display: none;
}

@media all and (max-width:320px) {
    .top-block__grid {
        display: none;
    }
    .top-block__grid_mini {
        display: block;
    }
}

.top-block__graph {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

.top-block__graph_mini {
    display: none;
    position: absolute;
    top: 0;
    left: 0;
}

@media all and (max-width:320px) {
    .top-block__graph {
        display: none;
    }
    .top-block__graph_mini {
        display: block;
    }
}

