.top-block__graph-text {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    position: absolute;
    height: 100%;
    top: 0;
    left: 0;
    font-size: 12px;
    line-height: 14px;
    color: #FFFFFF;
    margin-left: 20px;
}
